.product{
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: flex-end;
    width:100%;
    margin:10px;
    padding:20px;
    max-height: 400px;
    min-width:100px;
    background-color: white;
    z-index:1;
}
.product_rating{
    display:flex;
}
.product_info{
    height:100px;
    margin-bottom:15px;
}
.product_price{
    margin-top:5px;
}
.product > img {
    width:100%;
    max-height:200px;
    object-fit:contain;
    margin-bottom:15px;
}
.product > button{
    background-color: #f0c14b;
    border:1px solid;
    border-color:#a88734 #9c7e31 #846a29
}